<template>
  <div v-if="item.children && !item.hidden">
    <template>
      <!--  item  -->
      <!--  alwaysShow: true  -->
      <!-- children alwaysShow: true -->
      <!--  -->
      <!-- 20190707 note: icontitle -->
      <router-link v-if="hasOneShowingChildren(item.children, item) && (!onlyOneChild.children || onlyOneChild.noShowingChild) && !item.alwaysShow" :to="resolvePath(onlyOneChild.path)">
        <el-menu-item :index="item.path">
          <item :icon="item.children[0].meta.icon" :title="item.children[0].meta.title"></item>
        </el-menu-item>
      </router-link>

      <el-submenu v-else :index="item.path">
        <template slot="title">
          <item :icon="item.meta.icon" :title="item.meta.title"></item>
        </template>

        <template v-for="child of item.children" v-if="!child.hidden">
          <sidebar-item
            :item="child"
            :base-path="resolvePath(child.path)"
            v-if="child.children && child.children.length>0" />

          <router-link v-else :to="resolvePath(child.path)">
            <el-menu-item :index="child.path">
              <item :icon="child.meta.icon" :title="child.meta.title"></item>
            </el-menu-item>
          </router-link>
        </template>
      </el-submenu>

    </template>
  </div>
</template>
<script>
  import path from 'path'
  import Item from './item'
  export default {
    name: 'sidebar-item',
    components: {
      Item
    },
    props: {
      // 
      item: {
        type: Object,
        required: true
      },
      basePath: {
        type: String,
        default: ''
      }
    },
    data() {
      return {
        onlyOneChild: null
      }
    },
    methods: {
      hasOneShowingChildren(children, parent) {
        //  children  hidden 
        // 1children 
        // 2  '/login'  route  children
        // 3 children 1
        const showingChildren = children.filter(item => {
          if(item.hidden) {
            return false
          } else {
            // child
            this.onlyOneChild = item
            return true
          }
        })
        //  1  true
        if(showingChildren.length === 1) {
          return true
        }

        // 
        if(showingChildren.length === 0) {
          this.onlyOneChild = {
            ...parent,
            path: '',
            noShowingChild: true
          }
          return true
        }

        return false
      },
      resolvePath(routePath) {
        // console.log(path.resolve(this.basePath, routePath))
        return path.resolve(this.basePath, routePath)
      }
    },
    created() {
      // console.log(this.basePath)
    }
  }
</script>



