import Vue from 'vue'
import Router from 'vue-router'

import Login from '@/views/login'
import Layout from '@/layout/layout'

Vue.use(Router)

/**
 *  route 
 *
 * alwaysShow: true       // if set true, will always show the root menu, whatever its child routes length
 *                        // if not set alwaysShow, only more than ont route under the children
 *                        // it will becomes nested mode, otherwise not show the root menu
 *                        // true,1
 *                        // 
 *
 * hidden: true           // if set ture, 
 */

/**
 *  
 * @type {Array}
 */
export const constantRouterMap = [
  {
    path: '/login',
    name: 'login',
    hidden: true,
    component: Login,
    meta: {
      title: ''
    }
  },
  {
    path: '/404',
    component: () => import('@/views/error-page/404'),
    hidden: true
  },
  {
    path: '/',
    // hidden: true,
    component: Layout,
    redirect: '/home',
    children: [
      {
        path: 'home',
        name: 'home',
        component: () => import('@/views/homepage'),
        meta: {icon: 's-home', title: ''}
      }
    ]
  },
  {
    path: '/user',
    component: Layout,
    hidden: true,
    meta: {
      icon: 'tickets',
      title: ''
    },
    children: [
      {
        path: 'profile',
        name: 'Profile',
        component: () => import('@/views/user/profile'),
        meta: {icon: 'warning', title: ''}
      }
    ]
  },
  {
    path: '/introduction',
    component: Layout,
    redirect: '/introduction/index',
    // alwaysShow: true,
    meta: {
      icon: 'svg-aperture',
      title: ''
    },
    children: [
      {
        path: 'index',
        name: 'Introduction',
        component: () => import('@/views/introduction/index'),
        meta: {icon: 'svg-aperture', title: ''}
      }
    ]
  },
  {
    path: '/nested',
    component: Layout,
    meta: {
      icon: 'svg-layers',
      title: ''
    },
    children: [
      {
        path: 'menu1',
        meta: { icon: 'share', title: '1' },
        component: () => import('@/views/nested/menu1/index'),
        children: [
          {
            path: 'menu1-1',
            name: 'menu1-1',
            component: () => import('@/views/introduction/index'),
            meta: {icon: 'success', title: '1-1'}
          },
          {
            path: 'menu1-2',
            name: 'menu1-2',
            component: () => import('@/views/nested/menu1/menu1-2/index'),
            alwaysShow: true,
            meta: {icon: 'error', title: '1-2'},
            children: [
              {
                path: 'menu1-2-1',
                component: () => import('@/views/introduction/index'),
                name: 'menu1-2-1',
                meta: {icon: 'warning', title: '1-2-1'}
              }
            ]
          }
        ]
      },
      {
        path: 'menu2',
        name: 'menu2',
        component: () => import('@/views/user/add'),
        meta: {icon: 'star-on', title: '2'}
      }
    ]
  },
  {
    path: '/custom-component',
    component: Layout,
    meta: {
      icon: 'question',
      title: ''
    },
    redirect: '/custom-component/index',
    children: [
      {
        path: 'index',
        name: 'CustomComponent',
        component: () => import('@/views/custom-component/index'),
        meta: {icon: 'guide', title: ''}
      }
    ]
  },
  {
    path: '/echarts',
    component: Layout,
    redirect: 'index',
    alwaysShow: true,
    meta: {
      title: '',
      icon: 'svg-droplet'
    },
    children: [
      {
        path: 'index',
        name: '',
        component: () => import('@/views/echarts'),
        meta: {title: '', icon: 'svg-heart'}
      }
    ]
  },
  {
    path: '/rich-editor',
    component: Layout,
    alwaysShow: true,
    meta: {
      icon: 's-order',
      title: ''
    },
    redirect: 'quill',
    children: [
      {
        path: 'quill',
        name: 'QuillEditor',
        component: () => import('@/views/rich-editor/quill'),
        meta: {icon: 's-order', title: 'quill'}
      },
      {
        path: 'tinymce',
        name: 'TinyMCE',
        component: () => import('@/views/rich-editor/tinymce'),
        meta: {icon: 'svg-type', title: 'tinymce'}
      }
    ]
  },
  {
    path: '/futures',
    component: Layout,
    alwaysShow: true,
    meta: {
      icon: 's-grid',
      title: ''
    },
    children: [
      {
        path: 'draggable',
        name: 'draggable',
        component: () => import('@/views/futures/draggable'),
        meta: {icon: 'thumb', title: ''}
      },
      {
        path: '/clipboard',
        name: 'clipBoard',
        component: () => import('@/views/clipboard'),
        meta: {title: '', icon: 'document'}
      }
    ]
  }
]

export default new Router({
  // mode: 'history',  require service support
  // scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})

/**
 *  
 * @type {Array}
 */
export const asyncRouterMap = [
  {
    path: '/excel',
    component: Layout,
    redirect: '/excel/export-excel',
    alwaysShow: true,
    meta: {
      title: '',
      icon: 'date'
    },
    children: [
      {
        path: 'export-excel',
        name: '',
        component: () => import('@/views/excel/export-excel'),
        meta: {icon: 'download', title: ''}
      },
      {
        path: 'upload-excel',
        name: '',
        component: () => import('@/views/excel/upload-excel'),
        meta: {icon: 'upload', title: ''}
      },
      {
        path: 'merge-count',
        name: '&',
        component: () => import('@/views/excel/merge-count'),
        meta: {icon: 'upload', title: '&'}
      }
    ]
  },
  {
    path: '/theme',
    component: Layout,
    meta: {
      icon: 'document',
      title: 'theme'
    },
    children: [
      {
        path: 'index',
        component: () => import('@/views/theme/index'),
        name: 'Theme',
        meta: { title: '', icon: 'svg-pifu' }
      }
    ]
  },
  {
    path: '/test',
    component: Layout,
    meta: {
      icon: 'document',
      title: ''
    },
    children: [
      {
        path: 'index',
        component: () => import('@/views/test/index'),
        name: 'Test',
        meta: { title: '', icon: 'document' }
      }
    ]
  },
  { path: '*', redirect: '/404', hidden: true }
]



